p {
	display: flex;
	display: grid;
	font-size: 2em;
	transition: all .3s ease;
	color: blue;
	mix-blend-mode: multiply;
	filter: blur(4px);
	text-size-adjust: 100%;

	&:hover {
		transform: translateX(50px);
		color: red;
	}
}



